* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    background-color: #fffdfa;
    position: relative;
    font-family: Arial, sans-serif;
    scroll-behavior: smooth;
  }
  
  /* Background Circles */
  /* body::before,
    body::after {
      content: "";
      position: absolute;
      background-color: rgba(255, 235, 205, 0.5);
      border-radius: 50%;
      z-index: -1;
    }
    
    body::before {
      width: 300px;
      height: 300px;
      top: -150px;
      right: -150px;
    }
    
    body::after {
      width: 200px;
      height: 200px;
      bottom: -100px;
      left: -100px;
